body {
  overflow-x: hidden;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: "Rubik", sans-serif;
}

.NavBar {
  display: flex;
  position: fixed;
  width: 20%;
  height: 100%;
  background-image: linear-gradient(to right, rgb(29, 44, 44), rgb(26, 27, 27));
  color: #e7e7e7;
  box-shadow: 10px 10px 8px #888888;
  font-family: "Open Sans", sans-serif;
  flex-direction: column;
}
.NavBar::-webkit-scrollbar {
  width: 0.4em;
}
.gap {
  position: relative;
  background-color: darkgray;
  height: 1px;
  width: 80%;
  border-radius: 25px;
}
ul {
  position: relative;
  font-size: medium;
  list-style-type: none;
  margin-right: 10%;
  padding-left: 0;
  width: 100%;
  flex: 1 1;
}

.NavBar::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.NavBar::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}
.NavButton {
  align-items: flex-start;
  display: block;
  padding: 20px;
  border-radius: 15px;
  text-align: center;
  font-size: medium;
}
.NavButton:hover {
  cursor: pointer;
  background-image: linear-gradient(
    to right,
    rgb(96, 230, 230),
    rgb(59, 209, 139)
  );
  color: white;
  font-weight: 600;
}

@media (max-width: 500px) {
  .NavBar {
    width: 0;
  }
} ;

.GridContainer {
  display: grid;
  grid-template-columns: 2fr 2fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 0px;
  gap: 0px;
  height: 200px;
  width: 80%;
  position: relative;
}
.grid-image {
  margin-top: 47%;
  margin-left: 20%;
  grid-row: 1/3;
  grid-column: 1;
  height: 90px;
  width: 90px;
  border-radius: 50%;
}
img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.grid-item-1 {
  margin-top: 55%;
  margin-bottom: 5%;
  margin-left: 20px;
  height: 30%;
  font-weight: bold;
  color: azure;
  grid-row: 1;
  grid-column: 2;
}
.grid-item-2 {
  height: 50%;
  margin-left: 20px;
  margin-top: 0em;
  font-size: 0.7em;
  margin-bottom: 35%;
  grid-row: 2;
  grid-column: 2;
}

.OverviewContentBox {
  position: relative;
  display: inline-flex;
  color: black;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: stretch;
  align-content: stretch;
  width: 100%;
}
.ContentBox {
  position: absolute;
  top: 0;
  left: 20vw;
  width: 80%;
  background-color: rgb(252, 252, 252);
}
@media (max-width: 500px) {
  .ContentBox {
    left: 10px;
  }
}
@media (max-width: 550px) {
  .big-container {
    display: none;
  }
}
@media (min-width: 550px) {
  .small-container {
    display: none;
  }
}

.responsive-iframe-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 50px;
  height: 0;
  overflow: hidden;
}
.responsive-iframe-container iframe,
.vresponsive-iframe-container object,
.vresponsive-iframe-container embed {
  position: absolute;
  margin-left: 8%;
  top: 30px;
  left: 10px;
  width: 80%;
  height: 100%;
}

.ContentCardHalf {
  box-sizing: border-box;
  display: flex;
  position: relative;
  /* justify-content: space-evenly; */
  /* align-items: center; */
  margin: 20px;
  height: 300px;
  /* max-width: fit-content; */
  min-width: 500px;
  flex-basis: 42%;
  border: 3px rgb(85, 84, 84);
  box-shadow: 5px 5px 20px grey;
  background-color: rgb(241, 241, 247);
  flex-grow: 1;
  flex-shrink: 1;
  flex-wrap: wrap;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.InnerContainer {
  width: 100%;
  height: 100%;
}
.ContentCardFull {
  position: relative;
  margin: 20px;
  height: 300px;
  flex-basis: 92%;
  border: 3px rgb(85, 84, 84);
  box-shadow: 5px 5px 20px grey;
  background-color: rgb(241, 241, 247);
  flex-grow: 1;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.ContentTitle {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  height: 34px;
  box-sizing: border-box;
  padding: 8px 16px;
  background-color: rgb(167, 155, 155);
  color: black;
  display: inline-block;
  width: 100%;
  border-radius: inherit;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  font-weight: 550;
  font-size: medium;
}

.ProjectCard {
  margin: 20px;
  flex-basis: 250px;
  height: 100px;
  display: flex;
  position: relative;
  flex-direction: row;
  border-radius: 3px;
  border: 3px rgb(85, 84, 84);
  box-shadow: 5px 5px 20px grey;
  background-color: white;
  flex-grow: 1;
}
.Protitle {
  box-sizing: border-box;
  padding-left: 5px;
  padding-right: 0;
  font-size: small;
  font-weight: 550;
  color: rgb(138, 137, 137);
}
.ProStatus {
  width: 150px;
  height: 100%;
  position: relative;
  border-radius: inherit;
  padding: 10px;
  flex-grow: 1;
}
.ColoredNumber {
  padding: 5px;
  position: relative;
  display: block;
  font-size: x-large;
}
.StatusImage {
  position: relative;
  box-sizing: border-box;
  flex-basis: 100px;
  height: 100%;
  border-radius: inherit;
  padding: 10px;
  flex-grow: 0; /* do not grow   - initial value: 0 */
  flex-shrink: 0;
}

.StatusImage > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: inherit;
}

.ProjectDetails {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.StopwatchContainer {
  display: flex;
  flex-direction: row;
  flex: 1 1;
  height: 100%;
  width: 100%;
  color: rgb(116, 116, 116);
}
.ClockTime {
  box-sizing: border-box;
  position: relative;
  top: 34px;
  display: inline-block;
  flex-basis: 50%;
  height: 266px;
  font-size: 15px;
  font-weight: 550;
  padding-top: 40px;
  padding-left: 30px;
}
.timeColoring {
  color: red;
}
.ClockTime > p {
  position: relative;
}
@media (max-width: 900px) {
  .Stopwatch {
    margin-bottom: 40px;
  }
}
.Stopwatch {
  position: relative;
  top: 34px;
  margin: 0 auto;
  padding-left: 40px;
  padding-top: 10px;
  flex-basis: 50%;
}
.Stopwatch-header {
  font-size: medium;
  font-weight: bold;
}
.Stopwatch > p {
  font-size: small;
  font-weight: 550;
}
.Stopwatch button:hover {
  background-image: linear-gradient(
    to right,
    rgb(96, 230, 230),
    rgb(59, 209, 139)
  );
}
.Stopwatch button {
  background-color: #202b33;
  border: solid 1px transparent;
  border-radius: 4px;
  color: #ffffff;
  margin: 0 5px;
  cursor: pointer;
  padding: 10px 26px;
  font-size: 17px;
}
.Stopwatch-display {
  padding: 10px 0;
  padding-bottom: 5px;
  font-size: 40px;
  margin-bottom: 0;
  color: rgb(34, 185, 34);
}

.CContainer {
  position: absolute;
  top: 34px;
  height: 266px;
  width: 100%;
  margin: auto;
}

